.card {
    display: flex;
    flex-direction: column;
    background-color: aqua;
    border: 2px solid grey;
    border-radius: 5px;
    padding : 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
}
.card:hover {
    transform: scale(1.05);
  }